import '../styles/MainCategory.css';
import React, {useState} from 'react';
import '../styles/globalStyles.css'

const categories = [
    {
        id: 1,
        name: ' 1',
        subcategories: ['  1-1', '  1-2', ' ', ' '],
    },
    {
        id: 2,
        name: ' 2',
        subcategories: ['  2-1', '  2-2'],
    },
    {
        id: 3,
        name: ' 3',
        subcategories: ['  3-1', '  3-2'],
    },
    {
        id: 4,
        name: ' 4',
        subcategories: ['  4-1', '  4-2'],
    },
    {
        id: 5,
        name: ' 5',
        subcategories: ['  5-1', '  5-2'],
    },
    {
        id: 6,
        name: ' 6',
        subcategories: ['  6-1', '  6-2'],
    },
    {
        id: 7,
        name: ' 7',
        subcategories: ['  7-1', '  7-2'],
    },
    {
        id: 8,
        name: ' 8',
        subcategories: ['  8-1', '  8-2'],
    }
];
function MainCategory(){
    const [activeCategory, setActiveCategory] = useState(null);


    return (
        <div className="main-category">
         <div className="category-menu">
            {categories.map((category) => (
                <div
                    key={category.id}
                    className="category"
                    onMouseEnter={() => setActiveCategory(category.id)}
                    onMouseLeave={() => setActiveCategory(null)}
                >
                    {category.name}
                    {activeCategory === category.id && (
                        <ul className="subcategories">
                            {category.subcategories.map((subcategory, index) =>
                                (<li key={index}><a href="/">{subcategory}</a></li>))
                            }
                        </ul>
                    )}
                </div>
            ))}
         </div>
        </div>
    );
}

export default MainCategory;